/* SearchSection.module.css */

.section {
  background-color: white;
  border-bottom: 1px solid #e5e7eb;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 24px 16px;
}

@media (min-width: 640px) {
  .container {
    padding: 24px 24px;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 24px 32px;
  }
}

.content {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .content {
    flex-direction: row;
  }
}

.searchWrapper {
  position: relative;
  flex: 1;
  max-width: 512px;
}

.searchIcon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
  height: 20px;
  width: 20px;
  pointer-events: none;
}

.searchInput {
  width: 100%;
  padding-left: 40px;
  padding-right: 16px;
  padding-top: 12px;
  padding-bottom: 12px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  outline: none;
  transition: all 0.2s ease;
}

.searchInput:focus {
  outline: 2px solid #2563eb;
  outline-offset: 2px;
}

.newOrderButton {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #2563eb;
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.newOrderButton:hover {
  background-color: #1d4ed8;
}

.buttonIcon {
  height: 20px;
  width: 20px;
}